import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import React, { useState } from "react";
import TopNav from "../../components/TopNav";
import ModalNotification from "../../components/ModalNotification";
import PaymentCard from "../../components/PaymentCard";
import BottomPayment from "../../components/BottomPayment";
import makeStyles from "@mui/styles/makeStyles";
import failedImg from "../../assets/hand_fail.png";
import succImg from "../../assets/hand_succ.png";
import { useNavigate } from "react-router-dom";
import { connect } from "react-redux";
import { makeAppointment } from "../../redux/actions";
const useStyles = makeStyles(() => ({
  root: {
    // padding: "0 12px",
    paddingTop: "16px",
    // display: "flex",
    // justifyContent: "space-between",
    // alignItems: "center",
    // borderRadius: "6px",
  },
}));

const Payment = (props) => {
  const { stateBahasa, stateAppointment, actMakeAppointment, stateToken } =
    props;
  const classes = useStyles();
  const navigate = useNavigate();
  const [selected, setSelected] = useState(false);
  const [openSucc, setOpenSucc] = React.useState(false);
  const [openFail, setOpenFail] = React.useState({
    open: false,
    message: "",
  });
  const handleFail = (msg) => {
    setOpenFail({
      open: true,
      message: msg,
    });
  };
  const toLogin = () => {
    navigate("/login");
  };
  const handleAppointment = () => {
    return actMakeAppointment(
      {
        id_layanan: stateAppointment.layanan.id_poli,
        tanggal: stateAppointment.tanggal,
        doctor_id: stateAppointment.dokter.peg_id,
        is_bpjs: selected === "bpjs",
      },
      {
        success: (rkun_id) => navigate("/appointment-success/" + rkun_id),
        failed: (msg) => handleFail(msg),
      }
    );
  };
  return (
    <>
      <ModalNotification
        open={openSucc}
        setOpen={setOpenSucc}
        title={"Berhasil Mendaftar"}
        subtitle={""}
        img={succImg}
        actAbort={() => navigate("/home")}
      />
      <ModalNotification
        open={openFail.open}
        setOpen={(open) => setOpenFail({ ...openFail, open: open })}
        title={"Failed"}
        subtitle={openFail.message}
        img={failedImg}
        actAbort={!stateToken && toLogin}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title={
              stateBahasa === "indonesia" ? "Buat Janji" : "Make Apointment"
            }
            click={() => {
              navigate(-1);
            }}
          />
          <div>
            <Typography variant="subtitle2">
              {stateBahasa === "indonesia"
                ? "Penanggung yang dianjurkan"
                : "Suggested Assurance"}
            </Typography>
            <PaymentCard
              title="BPJS"
              price=""
              selected={selected === "bpjs"}
              disabled={false}
              onclick={() => setSelected("bpjs")}
            />
          </div>
          <div>
            <Typography variant="subtitle2">
              {stateBahasa === "indonesia"
                ? "Penanggung lainnya"
                : "Others Assurance"}
            </Typography>
            <PaymentCard
              title={stateBahasa === "indonesia" ? "Umum" : "Public"}
              price=""
              selected={selected === "umum"}
              disabled={false}
              onclick={() => setSelected("umum")}
            />
          </div>
          {!selected && (
            <Typography variant="body2" color="red">
              {stateBahasa === "indonesia"
                ? "*Mohon isi penanggung"
                : "*Select your assurance"}
            </Typography>
          )}
          <>
            <BottomPayment
              textAction="Buat Janji"
              type="total_payment"
              disabled={!selected}
              total={stateAppointment.total}
              action={handleAppointment}
            />
          </>
        </Stack>
      </Container>
    </>
  );
};
const mapStateToProps = (state) => {
  return {
    stateBahasa: state.app.bahasa,
    stateToken: state.auth.token,
    stateAppointment: state.register.appointment,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actMakeAppointment: (body, action) =>
      dispatch(makeAppointment(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Payment);
